.front, .back{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.prompt, .answer{
    font-size: var(--fs-heading-small);
    font-family: var(--font-heading);
    font-weight: 400;
    text-align: center;
}

.cardNumber{
    font-size: var(--fs-heading-large);
    color: var(--c-main-gray);
    font-weight: 600;
    opacity: .3;
    text-align: end;    
}

.showAnswerBtn{
    width: fit-content;
    align-self: center;
    background-color: var(--c-dark-gray);
    margin-bottom: 55px;
}

.evaluationWrapper{
    display: flex;
    gap: 50px;
    justify-content: center;
    margin-bottom: 55px;
}

.wrongIconWrapper:hover, .correctIconWrapper:hover{
    cursor: pointer;
}

